import {
	debounce
} from '@/common/utils.js';
export default {
	data() {
		return {
			flag: true,
			user_machine: '',
			machine_id: '',
			user_agreement: '',
			shade: false,
			sha: false,
			ress: '',
			scrollTop: 0,
			old: {
				scrollTop: 0,
			},
			verify: false,
			many:'0',
			num:'0.00',
			
		};
	},
	onShow() {
		var that=this;
		uni.request({
			url: this.url + "assets/",
			method:'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				console.log(res)
				that.num = parseFloat(res.data.fil_count).toFixed(2)||'0.00'
			}
		});
		uni.request({
			url: this.url + 'usermachines/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success: res => {
				console.log(res)
				this.user_machine = res.data.data.machine_datas[1];
				this.many=this.user_machine.length;
				if (this.user_machine.length == 0) {
					this.flag = false;
				}
				this.user_agreement = res.data.data.user_agreement;
				if (this.user_agreement == 0) {
					this.shade = true;
				} else if (this.user_agreement == 1) {
					this.shade = false;
				}
				this.ress = res.data.data.machine_datas[0];
				if (this.ress == 0) {
					this.verify = true;
				} else {
					this.verify = false;
				}
			}
		});
		
	},
	methods: {
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		backFront(){
			uni.navigateBack({
				delta:1
			})
		},
		salemachine:function(){
		    uni.showToast({
		    	title:'服务器出售功能暂未开放',
				icon:'none',
				duration:3000
		    })	
		},
		moveHandle: function(e) {
			e.preventDefault();
			e.stopPropagation();
		},
		linkToTransfer1: debounce(
			function() {
				uni.navigateTo({
					url: '../../pages/agreement/agreement'
				});
			},
			500,
			true
		),
		sure: function() {
			this.linkToTransfer1()
		},
		cancel: function() {
			uni.navigateBack({
				delta:1
			})
		},
		onNavigationBarButtonTap: function() {
			uni.navigateTo({
				url: '../../pages/agreement/agreement'
			});
		},
		linkToTransfer: debounce(
			function(item) {
				uni.navigateTo({
					url: '../machine-detail/machine-detail?machine_id=' + item.machine_id
				});
			},
			500,
			true
		),
		select: function(item) {
			this.linkToTransfer(item);
		},
		prompt: function() {
			this.sha = true;
		},
		can: function() {
			this.sha = false;
		},
		validation: function() {
			uni.request({
				url: this.url + 'usermachine/affirm/',
				method: 'POST',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success(res) {}
			});
			uni.showToast({
				title: '已验收'
			});
			this.sha = false;
			this.verify = false;
		}
	}
};
